.dribbble{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    height: 1248px;
}

.dribbble__content{
    width: auto;
    max-width: 80%;
    display: flex;
    height: auto;
    min-height: 80%;
    flex-direction: column;
    justify-content: space-between;
}

.dribbble__text{
    width: 636px;
    height: 212px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.dribbble__title{
    width: auto;
    max-width: 30%;
    font-size: 55px;
    font-weight: 700;
    line-height: 66.17px;
    text-align: left;
}

.dribbble__desc{
    width: auto;
    max-width: 100%;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: left;
    color: #8491A0;
}

.dribbble__link{
    width: auto;
    max-width: 30%;
}

.dribbble__link a{
    width: auto;
    max-width: 100%;

    padding-bottom: 10px;
    text-decoration: none;
    color: #FFFFFF;
    border-bottom: 1px solid #8491A0;
}

.dribbble__img,.dribbble__img img {
    width: 1076px;
    height: 676px;
    object-fit: cover;
}

@media (max-width: 768px){
    .dribbble{
        height: 1008px;
    }
    .dribbble__content{
        width: auto;
        max-width: 100%;
    }
    .dribbble__img,.dribbble__img img {
        width: 688px;
        height: 516px;
    }
}

@media (max-width: 360px){
    .dribbble{
        height: 974px;
    }
    .dribbble__content{
        min-height: 90%;
    }
    .dribbble__text{
        width: 320px;
    }
    .dribbble__text > *{
        padding: 15px 0;
    }
    .dribbble__title{
        max-width: 75%;
    }
    .dribbble__link{
        max-width: 100%;
    }
    .dribbble__img,.dribbble__img img {
        width: 320px;
        height: 516px;
        object-position: 0;
    }
  }